<script>

import request from "@/utils/request";
export default {

  // eslint-disable-next-line vue/multi-word-component-names
  name: "Map1",

  data() {
    return {

      map: {},
      lng: 116.40717, // 经度
      lat: 30.90469, // 纬度
      geometries : []
    }
  },
  async mounted() {
    // this.addMark()
    this.initMap()
    // this.addMarks()
  },
  methods: {
    // 地图初始化
    initMap() {
      // this.addMark()
      // eslint-disable-next-line no-undef
      const center = new TMap.LatLng(this.lat, this.lng)
      // 定义map变量，调用 TMap.Map() 构造函数创建地图
      // eslint-disable-next-line no-undef
      const map = new TMap.Map(document.getElementById('container'), {
        mapStyleId: 2,
        rotation: 0,//设置地图旋转角度
        pitch:0, //设置俯仰角度（0~45）
        zoom:4,//设置地图缩放级别
        center: center,//设置地图中心点坐标
      })
      this.map = map
      // eslint-disable-next-line no-undef
      var marker = new TMap.MultiMarker({
        id: 'marker-layer',
        map: map,
        styles: { //点标注的相关样式
              // eslint-disable-next-line no-undef
              "marker": new TMap.MarkerStyle({
                "width": 15,
                "height": 25,
                "anchor": { x: 16, y: 32 },
                // "src": "img/marker.png"
              })
            }
      });

      request.get("/chip/queryAll").then(res=>{
        if(res.code==="0"){
          this.$message({
            type:"success",
            message:"成功"
          })
          this.geometries=res.data
        }else {
          this.$message({
            type:"error",
            message:res.msg
          })
        }
        for(var i=0;i<this.geometries.length;i++){
          var data=this.geometries[i]
          console.log("for")
          marker.add({
            // eslint-disable-next-line no-undef
            position: new TMap.LatLng(data.latitude, data.longitude),
            styleId : "marker"

          });}
      });


      // for(var i=0;i<this.geometries.length;i++){
      //   var data=this.geometries[i]
      //   console.log("for")
      // marker.add({
      //   // eslint-disable-next-line no-undef
      //   position: new TMap.LatLng(data.latitude, data.longitude),
      //   styleId : "marker"
      //
      // });}
      console.log("afterfor")
      this.marker=marker

    },
  },
  addMark(){
    request.get("/chip/queryAll").then(res=>{
      if(res.code==="0"){
        this.$message({
          type:"success",
          message:"成功"
        })
        this.geometries=res.data
      }else {
        this.$message({
          type:"error",
          message:res.msg
        })
      }
      // this.load()
    })
  }
}

</script>

